import { NgModule } from '@angular/core';
import { Routes, RouterModule, ExtraOptions } from '@angular/router';
import { from } from 'rxjs';
import { BusinessComponent } from './business/business.component';
import { ContactComponent } from './contact/contact.component';
import { DigitalComponent } from './digital/digital.component';
import { HomepageComponent } from './homepage/homepage.component';
import { LandingpageComponent } from './homepage/landingpage/landingpage.component'
import { HrComponent } from './hr/hr.component';
import { PostComponent } from './post/post.component';
import { SmartComponent } from './smart/smart.component';
import { TurnComponent } from './turn/turn.component';
import { WhoweareComponent } from './whoweare/whoweare.component';


const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  {
    path: '', component: HomepageComponent, children: [
      { path: 'home', component: LandingpageComponent },
      { path: 'whoweare', component: WhoweareComponent },
      { path: 'smart', component: SmartComponent },
      { path: 'contact', component: ContactComponent },
      { path: 'digital', component: DigitalComponent },
      { path: 'transformandturnaround', component: TurnComponent },
      { path: 'postmerger', component: PostComponent },
      { path: 'humanresource', component: HrComponent },
      { path: 'business', component: BusinessComponent }
    ]
  }
];

const routesOption: ExtraOptions = {
  scrollPositionRestoration: 'enabled',
  anchorScrolling: 'enabled',
  scrollOffset: [0, 64]
}

@NgModule({
  imports: [RouterModule.forRoot(routes, routesOption)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
